import React from "react";
import "./mainform.css";

export default function mainform() {
  return (
    <div className="mainform">
      <div className="head">Please Contact Us</div>
      <div className="form">
        <form id="form">
          <input
            type="text"
            id="name"
            placeholder="Your Name"
            required="true"
          />
          <br />
          <input
            type="text"
            id="phoneno"
            placeholder="Phone Number"
            required="true"
          />
          <br />
          <input type="text" id="Email" placeholder="Email" required="true" />
          <br />
          <textarea placeholder="Your Message ..."></textarea>
          <br />
          <button className="formbuttton">SUBMIT</button>
        </form>
      </div>
    </div>
  );
}
