import React from "react";
import "./contact.css";
import Mainform from "./mainform";

export default function Contact() {
  return (
    <div className="contact">
      <div className="perosnalInfo">
        <div className="phoneno">
          <div className="phoneimg">
            <img
              src="https://www.freeiconspng.com/uploads/call-png-end-button-iphone-12.png"
              width="25"
              alt="Phone"
            />
          </div>
          <div className="phoneinfo">
            <div className="phonetitle">PHONE NO</div>
            <div className="phonetitleinfo">+91 8073891760</div>
          </div>
        </div>
        <div className="email">
          <div className="emailimg">
            <img
              src="https://www.freepnglogos.com/uploads/email-png/email-icon-png-transparent-email-icon-images-0.png"
              width="25"
              alt="Email"
            />
          </div>
          <div className="emailinfo">
            <div className="emailtitle">Email</div>
            <div className="emailtitleinfo">bvikasbehl@gmail.com</div>
          </div>
        </div>
        <div className="address">
          <div className="addressimg">
            <img
              src="https://www.freepnglogos.com/uploads/logo-website-png/logo-website-file-globe-icon-svg-wikimedia-commons-21.png"
              width="25"
              alt="Address"
            />
          </div>
          <div className="addressinfo">
            <div className="addresstitle">City</div>
            <div className="addresstitleinfo">Bellary</div>
          </div>
        </div>
      </div>
      <div className="mainform">
        <Mainform />
      </div>
    </div>
  );
}
