import React from "react";
import { MDBBtn, MDBIcon } from "mdb-react-ui-kit";
import { Link } from "react-scroll";
import styled from "styled-components";
import Typical from "react-typical";
import "./Profile.css";

export default function profile() {
  const Button = styled.button`
    background-color: darkgoldenrod;
    color: white;
    font-size: 20px;
    padding: 10px 60px;
    border-radius: 15px;
    margin: 10px 0px;
    cursor: pointer;
  `;

  return (
    <div className="profile">
      <div className="profile-container">
        <div className="profile-parent">
          <div className="profile-details">
            <div className="colz-icon">
              <MDBBtn
                style={{ backgroundColor: "#55acee" }}
                href="https://www.google.com/"
              >
                <MDBIcon className="me-1" fab icon="twitter" />
              </MDBBtn>
              <MDBBtn style={{ backgroundColor: "#3b5998" }} href="#">
                <MDBIcon className="me-1" fab icon="facebook-f" />
              </MDBBtn>
              <MDBBtn style={{ backgroundColor: "#ac2bac" }} href="#">
                <MDBIcon className="me-1" fab icon="instagram" />
              </MDBBtn>
              <MDBBtn style={{ backgroundColor: "#0082ca" }} href="#">
                <MDBIcon className="me-1" fab icon="linkedin-in" />
              </MDBBtn>
            </div>
            <div className="profile-details-name">
              <span className="primary-text">
                {" "}
                Hello,I'M <span className="highlighted-text">Vikas Behl</span>
              </span>
            </div>
            <div className="profile-detail-role">
              <span className="primary-text">
                {" "}
                <h1>
                  {""}
                  <Typical
                    loop={Infinity}
                    steps={[
                      "Ethusiastic Dev ",
                      1200,
                      "Full Stack Developer",
                      1200,
                      "Java 8",
                      1000,
                      "Spring Boot",
                      1000,
                      "Reactjs",
                      1000,
                      "Nodejs",
                      1000,
                    ]}
                  />
                </h1>
                <span className="profile-role-tagline">
                  {" "}
                  knack of bulding application with front and back end
                </span>
              </span>
            </div>
            <div className="profile-option">
              <Link to="contact" spy={true} smooth={true}>
                <button className="btn primary-btn">
                  {""}
                  Hire Me !
                </button>
              </Link>
              <a href="VikasResume.pdf">
                <Button variant="outline-info" className="btn highlighted-btn">
                  {" "}
                  get resume
                </Button>
              </a>
            </div>
          </div>
          <div className="profile-picture">
            <div className="profile-picture-background"></div>
          </div>
        </div>
      </div>
    </div>
  );
}
